import { PluginParameter } from "./PluginParameter";
/* jshint esversion: 6 */
export class ButtonParameter extends PluginParameter {
    constructor(owner, name, visibleName, exposed = true) {
        super(owner, name, "Button", visibleName, exposed);
        this.onclickFunction = () => { };
    }
    destroy() {
    }
    getParameterObject() {
        return {
            visibleName: this.visibleName,
            type: "ButtonParameter",
            name: this.name
        };
    }
    get type() {
        return "Button";
    }
    ;
    get onclick() {
        return this.onclickFunction;
    }
    set onclick(f) {
        this.onclickFunction = f;
    }
}
